import { Parser } from 'json2csv';
import moment from 'moment';
export function downloadAsJson(rows, fields, filenamePrefix) {
    var dateStr = moment().format('YYYY-MM-DD_HH:mm');
    var filename = filenamePrefix + "-" + dateStr + ".csv";
    console.log('downloadAsJson', { rows: rows, fields: fields, filenamePrefix: filenamePrefix, filename: filename });
    var json2csvParser = new Parser({ fields: fields, delimiter: ',' });
    var csv = json2csvParser.parse(rows);
    var a = document.createElement('a');
    a.textContent = 'download';
    a.download = filename;
    a.href = 'data:text/csv;charset=utf-8,' + escape(csv);
    document.body.appendChild(a);
    a.click();
}
